.label {
    display: block;
    margin-top: 10px;
    color: rgba(0, 0, 0, 0.473);
    font-weight: 500;
    font-size: 18px;
  }
  
  .input {
    display: block;
    margin-top: 10px;
    padding: 10px;
    width: 100%;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
    cursor: pointer;
    font-size: 15px;
  }
  
  .input:focus {
    outline-color:rgb(235, 69, 69);
  }